<template>
  <div class="loan-calculator-container">
    <div class="page-header">
      <h1>ğŸ’° è´·æ¬¾è®¡ç®—å™¨</h1>
      <p class="page-description">è®¡ç®—æˆ¿è´·ã€è½¦è´·ç­‰å„ç§è´·æ¬¾çš„æœˆä¾›å’Œåˆ©æ¯</p>
    </div>

    <div class="calculator-section">
      <div class="input-panel">
        <h3>è´·æ¬¾ä¿¡æ¯</h3>
        <div class="input-group">
          <div class="input-item">
            <label>è´·æ¬¾é‡‘é¢ (å…ƒ):</label>
            <el-input-number
              v-model="loanAmount"
              :min="1000"
              :max="10000000"
              :step="1000"
              @change="calculate"
              size="large"
            ></el-input-number>
          </div>
          
          <div class="input-item">
            <label>å¹´åˆ©ç‡ (%):</label>
            <el-input-number
              v-model="annualRate"
              :min="0.1"
              :max="20"
              :step="0.1"
              :precision="2"
              @change="calculate"
              size="large"
            ></el-input-number>
          </div>
          
          <div class="input-item">
            <label>è´·æ¬¾æœŸé™ (å¹´):</label>
            <el-input-number
              v-model="loanYears"
              :min="1"
              :max="30"
              @change="calculate"
              size="large"
            ></el-input-number>
          </div>
          
          <div class="input-item">
            <label>è¿˜æ¬¾æ–¹å¼:</label>
            <el-select v-model="repaymentType" @change="calculate" size="large">
              <el-option label="ç­‰é¢æœ¬æ¯" value="equal"></el-option>
              <el-option label="ç­‰é¢æœ¬é‡‘" value="principal"></el-option>
            </el-select>
          </div>
        </div>
      </div>

      <div class="result-panel">
        <h3>è®¡ç®—ç»“æœ</h3>
        <div class="result-summary">
          <div class="summary-item">
            <div class="summary-label">æœˆä¾›é‡‘é¢</div>
            <div class="summary-value primary">Â¥{{ formatMoney(monthlyPayment) }}</div>
          </div>
          <div class="summary-item">
            <div class="summary-label">æ€»åˆ©æ¯</div>
            <div class="summary-value">Â¥{{ formatMoney(totalInterest) }}</div>
          </div>
          <div class="summary-item">
            <div class="summary-label">è¿˜æ¬¾æ€»é¢</div>
            <div class="summary-value">Â¥{{ formatMoney(totalAmount) }}</div>
          </div>
        </div>

        <div class="payment-details" v-if="paymentSchedule.length > 0">
          <h4>è¿˜æ¬¾æ˜ç»† (å‰12æœŸ)</h4>
          <div class="schedule-table">
            <div class="table-header">
              <span>æœŸæ•°</span>
              <span>æœˆä¾›</span>
              <span>æœ¬é‡‘</span>
              <span>åˆ©æ¯</span>
              <span>ä½™é¢</span>
            </div>
            <div 
              v-for="(payment, index) in paymentSchedule.slice(0, 12)" 
              :key="index"
              class="table-row"
            >
              <span>{{ payment.period }}</span>
              <span>{{ formatMoney(payment.payment) }}</span>
              <span>{{ formatMoney(payment.principal) }}</span>
              <span>{{ formatMoney(payment.interest) }}</span>
              <span>{{ formatMoney(payment.balance) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoanCalculatorView',
  metaInfo: {
    title: 'è´·æ¬¾è®¡ç®—å™¨ - æˆ¿è´·è½¦è´·æœˆä¾›åˆ©æ¯ä¸€é”®ç®—ï¼Œç­‰é¢æœ¬æ¯/ç­‰é¢æœ¬é‡‘ç²¾å‡†å¯¹æ¯”',

    meta: [
      {
        name: 'description',
        content: 'ä¸“ä¸šè´·æ¬¾è®¡ç®—å™¨ï¼Œå®æ—¶è®¡ç®—æˆ¿è´·ã€è½¦è´·ã€ä¿¡ç”¨è´·ç­‰æœˆä¾›æ€»é¢ã€åˆ©æ¯æ€»é¢ï¼Œæ”¯æŒç­‰é¢æœ¬æ¯å’Œç­‰é¢æœ¬é‡‘ä¸¤ç§è¿˜æ¬¾æ–¹å¼ï¼Œè‡ªåŠ¨å¯¹æ¯”æ€»åˆ©æ¯å·®é¢ï¼Œè¾“å…¥é‡‘é¢å¹´é™åˆ©ç‡ä¸‰ç§’å‡ºç»“æœï¼Œå®Œå…¨å…è´¹æ— å¹¿å‘Šï¼',
        vmid: 'description'
      },
      {
        name: 'keywords',
        content: 'è´·æ¬¾è®¡ç®—å™¨,æˆ¿è´·è®¡ç®—å™¨,æœˆä¾›è®¡ç®—å™¨,ç­‰é¢æœ¬æ¯è®¡ç®—å™¨,ç­‰é¢æœ¬é‡‘è®¡ç®—å™¨,æˆ¿è´·åˆ©æ¯è®¡ç®—,è½¦è´·è®¡ç®—å™¨,è´·æ¬¾æœˆä¾›è®¡ç®—,æå‰è¿˜æ¬¾è®¡ç®—å™¨,æˆ¿è´·è®¡ç®—2025',
        vmid: 'keywords'
      }
    ]
  },
  data() {
    return {
      loanAmount: 1000000,
      annualRate: 4.9,
      loanYears: 30,
      repaymentType: 'equal',
      monthlyPayment: 0,
      totalInterest: 0,
      totalAmount: 0,
      paymentSchedule: []
    }
  },
  mounted() {
    this.calculate()
  },
  methods: {
    calculate() {
      if (this.repaymentType === 'equal') {
        this.calculateEqualPayment()
      } else {
        this.calculateEqualPrincipal()
      }
    },

    calculateEqualPayment() {
      const monthlyRate = this.annualRate / 100 / 12
      const totalMonths = this.loanYears * 12
      
      // ç­‰é¢æœ¬æ¯æœˆä¾›è®¡ç®—
      this.monthlyPayment = this.loanAmount * 
        (monthlyRate * Math.pow(1 + monthlyRate, totalMonths)) /
        (Math.pow(1 + monthlyRate, totalMonths) - 1)
      
      this.totalAmount = this.monthlyPayment * totalMonths
      this.totalInterest = this.totalAmount - this.loanAmount

      // ç”Ÿæˆè¿˜æ¬¾è®¡åˆ’
      this.paymentSchedule = []
      let balance = this.loanAmount
      
      for (let i = 1; i <= totalMonths; i++) {
        const interestPayment = balance * monthlyRate
        const principalPayment = this.monthlyPayment - interestPayment
        balance -= principalPayment
        
        this.paymentSchedule.push({
          period: i,
          payment: this.monthlyPayment,
          principal: principalPayment,
          interest: interestPayment,
          balance: Math.max(0, balance)
        })
      }
    },

    calculateEqualPrincipal() {
      const monthlyRate = this.annualRate / 100 / 12
      const totalMonths = this.loanYears * 12
      const monthlyPrincipal = this.loanAmount / totalMonths
      
      // ç­‰é¢æœ¬é‡‘é¦–æœˆæœˆä¾›
      this.monthlyPayment = monthlyPrincipal + this.loanAmount * monthlyRate
      
      // ç”Ÿæˆè¿˜æ¬¾è®¡åˆ’
      this.paymentSchedule = []
      let balance = this.loanAmount
      let totalInterestSum = 0
      
      for (let i = 1; i <= totalMonths; i++) {
        const interestPayment = balance * monthlyRate
        const payment = monthlyPrincipal + interestPayment
        balance -= monthlyPrincipal
        totalInterestSum += interestPayment
        
        this.paymentSchedule.push({
          period: i,
          payment: payment,
          principal: monthlyPrincipal,
          interest: interestPayment,
          balance: Math.max(0, balance)
        })
      }
      
      this.totalInterest = totalInterestSum
      this.totalAmount = this.loanAmount + this.totalInterest
    },

    formatMoney(amount) {
      return Math.round(amount).toLocaleString('zh-CN')
    }
  }
}
</script>

